import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  ReanderField,
  ReanderSelect,
} from "../../../../components/helpers/field";
import { getItem } from "../../../../components/helpers/function";
import ValidasiTitipan from "../../../../Validasi/Titipan/ValidasiTitipan";

// filter_titipan
const maptostate = (state) => {
  let data = getItem('filter_titipan') || []
  return {
    initialValues: {
      filter: data.filter,
      pencarian: data.pencarian,
    }
  }
}
const filterPencarian = [
  {
    value: "NO_HP",
    name: "NO HP",
  },
  {
    value: "NAMA_CUSTOMER",
    name: "NAMA CUSTOMER",
  },
  {
    value: "NO_TITIP",
    name: "NO TITIPAN",
  },
 
];
class FilterTitipan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      type_pembayaran: "CASH",
    };
  }

  render() {
    return (
      <>
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
            <div className="col-4">
              <Field
                name="filter"
                label="Filter"
                placeholder="Pilih Filter"
                options={filterPencarian}
                component={ReanderSelect}
              />
            </div>
            <div className="col-4">
              <Field
                name="pencarian"
                label="Pencarian"
                placeholder="Masukan Pencarian"
                component={ReanderField}
              />
            </div>
            <div className="col-4">
              <label>&nbsp;</label>
              <button
                className="btn btn-primary btn-block"
                disabled={this.props.isLoading}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari"
                )}
              </button>
            </div>
          </div>
        </form>
      </>
    );
  }
}

FilterTitipan = reduxForm({
  form: "FilterTitipan",
  enableReinitialize: true,
  validate:ValidasiTitipan
})(FilterTitipan);

export default connect(maptostate)(FilterTitipan);
