import {
  React,
  reduxForm,
  reset,
  Tabel,
  ModalGlobal,
  hideModal,
  showModal,
  Component,
  connect,
  postData,
  ToastNotification,
  localStoragedecryp,
  localStorageencryp,
  Panel,
  getItem,
  setItem,
} from "../../../components/helpers/library";
import { setCamera } from "../../../actions/databarang_action";
import { getDataSales } from "../../../actions/datamaster_action";
import { setBarangPenjualan } from "../../../actions/penjualan_action";
import ValidasiDataUsers from "../../../Validasi/ManageUsers/ValidasiDataUsers";
import AmbilTitipan from "./AmbilTitipan";
import ModalDataCustomer from "./ModalDataCustomer";
import ModalPembayaran from "./ModalPembayaran";
import ModalTambahBarang from "./ModalTambahBarang";
import { postData2 } from "../../../config/axios";

const maptostate = (state) => {
  return {
    setBarangPenjualan: state.penjualan.setBarangPenjualan,
    DataSales: state.datamaster.getDataSales,
    hideModal: state.datamaster.modalDialog,
  };
};

class HeadTransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      isLoadingBatal: false,
      isLoadingPembayran: false,
      isLoadingPembayranTitipan: false,
      isLoadingDataCustomer: false,
      FormModal: "",
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
  }
  // getBarcode(e) {
  //   getData("barang/get/by-kode-barcode/" + e)
  //     .then((res) => {
  //       if (res.data.length === 0) {
  //         ToastNotification("info", "Data Yang Dicari Tidak Ada");
  //         this.props.change("nama_barang", "");
  //         this.props.change("nama_atribut", "");
  //         this.props.change("berat_atribut", "");
  //         this.props.change("harga_atribut", "");
  //       } else {
  //         // console.log(res.data);
  //         res.data.forEach((list, index) => {
  //           this.props.change("nama_barang", list.nama_barang);
  //           this.props.change("nama_atribut", list.nama_atribut);
  //           this.props.change("berat_atribut", list.berat_atribut);
  //           this.props.change("harga_atribut", list.harga_atribut);
  //         });
  //       }
  //     })
  //     .catch((err) => {
  //       ToastNotification("info", "Data Yang Dicari Tidak Ada");
  //     });
  // }

  hideModa() {
    this.props.dispatch(hideModal());
  }
  handleSubmit(data) {
    localStorageencryp("data_customer_tmp", JSON.stringify(data));
    this.setState({
      isLoadingDataCustomer: true,
    });

    setTimeout(() => {
      this.setState({
        isLoadingDataCustomer: false,
      });
      this.props.dispatch(hideModal());
    }, 500);
  }
  simpanBarang(data) {
    let saveImage = {
      kode_barcode: data.kode_barcode,
      dataimage: data.foto,
    };
    data = {
      no_titip_group: "-",
      kode_barcode: data.kode_barcode || 0,
      nama_barang: data.nama_barang || "-",
      nama_atribut: data.nama_atribut || "-",
      berat_jual: parseFloat(data.berat) || 0,
      berat_atribut: parseFloat(data.berat_atribut) || 0,
      harga_atribut: parseInt(data.harga_atribut) || 0,
      harga_jual: parseInt(data.harga_jual) || 0,
      harga_pergram: parseInt(data.harga_pergram) || 0,
      ongkos: parseInt(data.ongkos) || 0,
      total: parseInt(data.total) || 0,
      status_premi: data.premi ? "PREMI" : "NONPREMI",
    };

    if (data.harga_jual === 0) {
      ToastNotification("info", "Harga Jual Tidak Boleh 0");
      return false;
    } else {
      if (data.foto !== null) {
        postData("imagebarang/add", saveImage)
          .then((res) => {
            // console.log(res.data);
            this.props.dispatch(setCamera(false));
            this.props.change("foto", "");
          })
          .catch((error) => {
            // console.log(error);
          });
      }
    }

    let cek =
      localStoragedecryp("data_barang_penjualan_tmp").length === 0
        ? []
        : JSON.parse(localStoragedecryp("data_barang_penjualan_tmp"));
    let hasil_cek = cek.findIndex(
      (hasil) => hasil.kode_barcode === data.kode_barcode
    );
    if (hasil_cek === -1) {
      // this.props.dispatch(hideModal());
      // this.props.change('kode_barcode','INI')
      this.props.dispatch(reset("ModalTambahBarang"));
      document.getElementById("kode_barcode").focus();
      cek.push(data);
      localStorageencryp("data_barang_penjualan_tmp", JSON.stringify(cek));
      this.props.dispatch(setBarangPenjualan(cek));
      this.props.change("kode_barcode", "");
    } else {
      document.getElementById("kode_barcode").focus();
      ToastNotification("info", "Barang Sudah Ada Di Tabel");
      // this.props.dispatch(reset('ModalTambahBarang'))
      // document.getElementById('kode_barcode').value = data.kode_barcode
    }
  }

  bayarTitipan() {
    this.setState({
      isLoadingPembayranTitipan: true,
    });

    // let cash = 0;
    // if (data.cash > data.grand_total_pembayaran) {
    //   cash = data.grand_total_pembayaran
    // } else {
    //   cash = data.cash
    // }
    // let harus_bayar = document.getElementById("harus_bayar_rp").value;
    // let harus_bayar2 = harus_bayar.replace("Rp.", "");
    // let harus_bayar3 = harus_bayar2.replaceAll(".", "");

    // console.log();
    let pembayaran = getItem("trx_pemabayran_penjualan");
    let data_pembayaran = [];
    let total_pembayarn = 0;
    pembayaran.forEach((element) => {
      let row = {
        jenis: element.jenis,
        keterangan: element.keterangan,
        jumlah_rp: element.nominal,
      };
      total_pembayarn = element.nominal + total_pembayarn;
      data_pembayaran.push(row);
    });
    // let sisa = parseInt(harus_bayar3) - parseInt(total_pembayarn);

    // if (parseInt(sisa) > 0) {
    //   ToastNotification(
    //     "info",
    //     "Pembayaran Masih Kurang Rp." + parseInt(sisa).toLocaleString("ID-id")
    //   );
    //   this.setState({
    //     isLoadingPembayran: false,
    //   });
    //   return false;
    // }

    let data_customer = JSON.parse(localStoragedecryp("data_customer_tmp"));
    let data_barang_penjualan = JSON.parse(
      localStoragedecryp("data_barang_penjualan_tmp")
    );

    let detail_barang = [];
    data_barang_penjualan.forEach((item, index) => {
      let row = {
        kode_barcode: item.kode_barcode,
        berat: item.berat_jual,
        berat_atribut: item.berat_atribut,
        ongkos: item.ongkos,
        harga_jual: item.harga_jual,
        harga_atribut: item.harga_atribut,
      };
      detail_barang.push(row);
    });
    let datapenjualan = {
      status: "TITIPAN",
      kode_sales: data_customer.kode_sales,
      kode_customer:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      nama_customer: data_customer.nama_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      detail_barang: detail_barang,
      pembayaran: data_pembayaran,
    };

    // console.log(datapenjualan);

    postData2("titip/create", datapenjualan)
      .then((res) => {
        // console.log(res.data);
        localStorageencryp("data_barang_penjualan_tmp", JSON.stringify([]));
        ToastNotification("success", res.data);
      })
      .then(() => {
        this.props.dispatch(hideModal());
        this.props.dispatch(setCamera(false));
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        localStorageencryp("data_customer_tmp", JSON.stringify([]));
        localStorageencryp("total_dp", JSON.stringify([]));
        localStorageencryp("trx_pemabayran_penjualan", JSON.stringify([]));
        localStorageencryp("filter_titipan", JSON.stringify([]));
      })
      .then(() => {
        this.setState({
          isLoadingPembayranTitipan: false,
        });
        this.props.dispatch(setBarangPenjualan([]));
      })
      .catch((err) => this.errorhandling(err));
  }
  simpanPembayaran(data) {
    this.setState({
      isLoadingPembayran: true,
    });
    // let cash = 0;
    // if (data.cash > data.grand_total_pembayaran) {
    //   cash = data.grand_total_pembayaran
    // } else {
    //   cash = data.cash
    // }
    let harus_bayar = document.getElementById("harus_bayar_rp").value;
    let harus_bayar2 = harus_bayar.replace("Rp.", "");
    let harus_bayar3 = harus_bayar2.replaceAll(".", "");

    // console.log();
    let pembayaran = getItem("trx_pemabayran_penjualan");
    let data_pembayaran = [];
    let total_pembayarn = 0;
    pembayaran.forEach((element) => {
      let row = {
        jenis: element.jenis,
        keterangan: element.keterangan,
        jumlah_rp: element.nominal,
      };
      total_pembayarn = element.nominal + total_pembayarn;
      data_pembayaran.push(row);
    });
    let sisa = parseInt(harus_bayar3) - parseInt(total_pembayarn);

    if (parseInt(sisa) > 0) {
      ToastNotification(
        "info",
        "Pembayaran Masih Kurang Rp." + parseInt(sisa).toLocaleString("ID-id")
      );
      this.setState({
        isLoadingPembayran: false,
      });
      return false;
    }

    let data_customer = JSON.parse(localStoragedecryp("data_customer_tmp"));
    let data_barang_penjualan = JSON.parse(
      localStoragedecryp("data_barang_penjualan_tmp")
    );

    let detail_barang = [];
    data_barang_penjualan.forEach((item, index) => {
      let row = {
        no_titip_group: item.no_titip_group,
        kode_barcode: item.kode_barcode,
        berat: item.berat_jual,
        berat_atribut: item.berat_atribut,
        ongkos: item.ongkos,
        harga_jual: item.harga_jual,
        harga_atribut: item.harga_atribut,
      };
      detail_barang.push(row);
    });
    let datapenjualan = {
      status: "PENJUALAN",
      kode_sales: data_customer.kode_sales,
      kode_member:
        data_customer.jenis_pelanggan === "NONMEMBER"
          ? data_customer.jenis_pelanggan
          : data_customer.kode_customer,
      nama_customer: data_customer.nama_customer,
      alamat_customer: data_customer.alamat_customer,
      no_hp: data_customer.no_hp,
      detail_barang: detail_barang,
      pembayaran: data_pembayaran,
    };

    // console.log(datapenjualan);
    // if (parseInt(data.cash) < parseInt(data.grand_total_pembayaran)) {
    //   let cash_bayar =
    //     parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
    //   ToastNotification(
    //     "info",
    //     "Pembayaran Masih Kurang Rp." +
    //       parseInt(cash_bayar).toLocaleString("ID-id")
    //   );
    //   this.setState({
    //     isLoadingPembayran : false
    //   })
    //   return false;
    // }
    // localStorage.setItem("hasil", JSON.stringify(datapenjualan));
    postData("penjualan/simpan", datapenjualan)
      .then((res) => {
        console.log(res.data);
        localStorageencryp("data_barang_penjualan_tmp", JSON.stringify([]));
        ToastNotification("success", res.data.message);
      })
      .then(() => {
        this.props.dispatch(hideModal());
        this.props.dispatch(setCamera(false));
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        localStorageencryp("data_customer_tmp", JSON.stringify([]));
        localStorageencryp("total_dp", JSON.stringify([]));
        localStorageencryp("trx_pemabayran_penjualan", JSON.stringify([]));
        localStorageencryp("filter_titipan", JSON.stringify([]));
      })
      .then(() => {
        this.setState({
          isLoadingPembayran: false,
        });
        this.props.dispatch(setBarangPenjualan([]));
      })
      .catch((err) =>
        this.errorhandling(err.response.data || "Koneksi Bermasalah")
      );
    // localStorage.setItem("tmp_pembayaran_transaksi_penjualan",JSON.stringify(hasil));
  }

  errorhandling(error) {
    this.setState({
      isLoadingPembayran: false,
      isLoadingPembayranTitipan: false,
    });
    ToastNotification("info", error || "Terjadi Kesalahan Saat Mengirim Data");
  }
  ShowModalBarang() {
    this.props.dispatch(showModal());
    this.props.dispatch(reset("ModalTambahBarang"));
    this.setState({ FormModal: "ShowModalBarang" });
  }
  ShowModalCustomer() {
    this.setState({ FormModal: "ShowModalCustomer" });
    this.props.dispatch(showModal());
  }
  showModalTitipan() {
    this.setState({ FormModal: "AmbilTitipan" });
    this.props.dispatch(showModal());
  }
  showModalPembayaran() {
    // document.getElementById('jenis_pembayaran').focus();
    let customer =
      localStoragedecryp("data_customer_tmp").length === 0
        ? []
        : JSON.parse(localStoragedecryp("data_customer_tmp"));
    let barang =
      localStoragedecryp("data_barang_penjualan_tmp").length === 0
        ? []
        : JSON.parse(localStoragedecryp("data_barang_penjualan_tmp"));
    if (customer.length === 0 || barang.length === 0) {
      ToastNotification(
        "info",
        "Data Customer Atau Data Barang Belum Diisi !!!"
      );
    } else {
      this.setState({ FormModal: "ShowModalPembayaran" });
      this.props.dispatch(showModal());
    }
  }
  batalPenjualan() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      setItem("total_dp", 0);
      localStorageencryp("trx_pemabayran_penjualan", JSON.stringify([]));
      localStorageencryp("data_customer_tmp", JSON.stringify([]));
      this.props.dispatch(setBarangPenjualan([]));
      localStorageencryp("data_barang_penjualan_tmp", JSON.stringify([]));
      this.setState({
        isLoadingBatal: false,
      });
    }, 500);
  }
  render() {
    return (
      <Panel>
        <div className="row">
          <div className="col-12">
            <br />
            <Tabel
              keyField="kode_barcode"
              exportCsv={false}
              data={
                this.props.setBarangPenjualan.length === 0
                  ? localStoragedecryp("data_barang_penjualan_tmp").length === 0
                    ? []
                    : JSON.parse(
                        localStoragedecryp("data_barang_penjualan_tmp")
                      )
                  : this.props.setBarangPenjualan || []
              }
              btnOptional2={true}
              btnTextOptional2="Ambil Titipan"
              handleClickOptional2={() => this.showModalTitipan()}
              tambahData={true}
              btnText="Data Customer"
              columns={this.props.columns}
              btnOptional={true}
              namaCustomer={true}
              jenisCustomer={true}
              form="Penjualan"
              btnTextOptional="Data Barang"
              handleClick={() => this.ShowModalCustomer()}
              handleClickOptional={() => this.ShowModalBarang()}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
        </div>

        <ModalGlobal
          size={
            this.state.FormModal === "ShowModalCustomer"
              ? "xl"
              : this.state.FormModal === "ShowModalBarang"
              ? "xl"
              : this.state.FormModal === "AmbilTitipan"
              ? "xl"
              : "l"
          }
          title={<TitleModal state={this.state.FormModal} />}
          content={
            this.state.FormModal === "ShowModalCustomer" ? (
              <ModalDataCustomer
                isLoadingDataCustomer={this.state.isLoadingDataCustomer}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            ) : this.state.FormModal === "ShowModalBarang" ? (
              <ModalTambahBarang
                isLoadingTopBar={this.props.isLoadingTopBar}
                hideModal={() => this.hideModa()}
                onSubmit={(data) => this.simpanBarang(data)}
              />
            ) : this.state.FormModal === "AmbilTitipan" ? (
              <AmbilTitipan />
            ) : (
              <ModalPembayaran
                isLoadingPembayranTitipan={this.state.isLoadingPembayranTitipan}
                isLoadingPembayran={this.state.isLoadingPembayran}
                bayarTitipan={(data) => this.bayarTitipan(data)}
                simpanPembayaran={(data) => this.simpanPembayaran(data)}
              />
            )
          }
        />
        <div className="row">
          <div className="col-9">
            <button
              onClick={() => this.showModalPembayaran()}
              className="btn btn-primary"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data Penjualan"
              )}
            </button>
            &nbsp;
            <button
              onClick={() => this.batalPenjualan()}
              className="btn btn-warning"
              disabled={this.state.isLoadingBatal}
              type="button"
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Reset
                </>
              ) : (
                "Reset"
              )}
            </button>
          </div>
        </div>
      </Panel>
    );
  }
}

function TitleModal(data, onSubmit) {
  // console.log(state.state);
  if (data.state === "ShowModalCustomer") {
    return "Form Data Customer";
  } else if (data.state === "ShowModalBarang") {
    return "Form Data Barang";
  } else if (data.state === "AmbilTitipan") {
    return "Form Data Ambil Titipan";
  } else {
    return "Form Pembayaran";
  }
}

HeadTransaksiPenjualan = reduxForm({
  form: "HeadTransaksiPenjualan",
  enableReinitialize: true,
  validate: ValidasiDataUsers,
})(HeadTransaksiPenjualan);
export default connect(maptostate)(HeadTransaksiPenjualan);
