import {
  React,
  reduxForm,
  Component,
  connect,
  getItem,
  setItem,
  Tabel,
  Swal,
  ToastNotification,
} from "../../../components/helpers/library";

import ValidasiPembayaran from "../../../Validasi/Penjualan/ValidasiPembayaran";
import HeadInputPembayaran from "./HeadInputPembayaran";

// data_barang_penjualan_tmp

const maptostate = (state) => {
  let data_barang = getItem("data_barang_penjualan_tmp") || [];
  let status = "PENJUALAN";
  data_barang.forEach((element) => {
    if (element.no_titip_group === "-") {
      status = "PENJUALAN";
    } else {
      status = "TITIPAN";
    }
  });
  return {
    status: status,
  };
};
class ModalPembayaran extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      type_pembayaran: "CASH",
      kembalian: "",
      columns: [
        {
          dataField: "jenis",
          text: "Jenis",
          footerAttrs: {
            colSpan: "2",
          },
          footer: "Grand Total",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "nominal",
          text: "Total",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("id-ID"),
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    disabled={row.keterangan === "TITIPAN" ? true : false}
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
  }
  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      // console.log(id);
      if (result.isConfirmed) {
        let data = getItem("trx_pemabayran_penjualan");
        data.splice(id, 1);
        setItem("trx_pemabayran_penjualan", data);
        this.setState({
          status: "berhasil",
        });
        // deleteLocalItemBarcode("data_barang_hancur_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }
  simpanPembayaran(row) {
    if (row.jenis !== "CASH") {
      if (row.kembali.replace(/[^0-9]/g, "") - row.cash < 0) {
        ToastNotification("info", "Pembayaran tidak boleh lebih dari sisa");
        return false;
      }
    }
    // console.log(data);
    let data =
      JSON.parse(localStorage.getItem("trx_pemabayran_penjualan")) || [];
    let index = data.findIndex((hasil) => hasil.jenis === row.jenis);
    if (index === -1) {
      let keterangan = "";
      if (row.jenis === "CASH") {
        keterangan = "CASH";
      } else if (row.jenis === "TRANSFER") {
        keterangan = row.no_card + " - " + row.nama_bank;
      } else {
        keterangan = row.nama_bank;
      }
      let hasil = {
        jenis: row.jenis,
        nominal: parseInt(row.cash),
        nama_bank: row.nama_bank,
        no_card: row.no_card,
        keterangan: keterangan,
      };
      data.push(hasil);
      this.setState({
        masuk: true,
        kembalian: row.kembali,
      });
      setItem("trx_pemabayran_penjualan", data);
    } else {
      let data_pembayaran = getItem("trx_pemabayran_penjualan");
      data_pembayaran = data_pembayaran.map((element2) => {
        if (element2.jenis === row.jenis) {
          element2.nominal += parseInt(row.cash);
        }
        return element2;
      });
      setItem("trx_pemabayran_penjualan", data_pembayaran);
      this.setState({
        status: true,
        kembalian: row.kembalian,
      });
      // ToastNotification(
      //   "info",
      //   "Pembayaran dengan " + row.jenis + " sudah ada"
      // );
    }
  }

  render() {
    return (
      <>
        <HeadInputPembayaran onSubmit={(data) => this.simpanPembayaran(data)} />
        <hr />
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <Tabel
            // handleClick={() => this.simpanpembbayaran()}
            keyField="jenis"
            // tambahData={true}
            // btnText="Tambah Pembyaran"
            exportCsv={false}
            data={getItem("trx_pemabayran_penjualan") || []}
            columns={this.state.columns}
            empty={true}
            pagination="off"
            textEmpty="Data Pembayaran Kosong"
          />
          <div className="row">
            <div className="col-6 text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.isLoadingPembayran}
                onClick={() => this.props.simpanPembayaran()}
                type="button"
              >
                {this.props.isLoadingPembayran ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Diproses
                  </>
                ) : (
                  "Bayar Sekarang"
                )}
              </button>
            </div>
            <div className="col-6">
              <button
                className="btn btn-outline-primary"
                disabled={
                  this.props.isLoadingPembayranTitipan ||
                  this.props.status === "TITIPAN"
                    ? true
                    : false
                }
                onClick={() => this.props.bayarTitipan()}
                type="button"
              >
                {this.props.isLoadingPembayranTitipan ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Diproses
                  </>
                ) : (
                  "Bayar DP"
                )}
              </button>
            </div>
          </div>
        </form>
      </>
    );
  }
}

ModalPembayaran = reduxForm({
  form: "ModalPembayaran",
  enableReinitialize: true,
  validate: ValidasiPembayaran,
})(ModalPembayaran);

export default connect(maptostate)(ModalPembayaran);
