const ValidasiTitipan = (value) => {
    const errors = {};
    if (!value.filter) {
        errors.filter = "Filter Pencarian Tidak Boleh Kosong";
    }
    if (!value.pencarian) {
        errors.pencarian = "Filter Pencarian Tidak Boleh Kosong";
    }
    return errors;
}

export default ValidasiTitipan;


